.heroSection {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    align-items: flex-start;
    justify-content: flex-end;
    padding: 10% 5%;
    color: white;
    background-image: linear-gradient(#141414, #090909);
    font-size: 2.5rem;
    font-family: 'Manrope';
    overflow: hidden;
}

h1 {
    font-weight:500;
    overflow: hidden;
}

.textLoop {
    color: #3C91E6;
    font-weight:500;
}

.frontendtext{
    display: flex;
    flex-direction: row;
    gap: 1rem;
}

@media (min-width: 768px) and (max-width: 1360px) {
    .heroSection{
        padding: 5%;
        font-size: 3rem;
    }
    #hoverme {
        display: none;
    }
}

@media (max-width: 768px) {
    .heroSection{
        padding: 5%;
        font-size: 1.8rem;
    }

    .frontendtext{
        display: flex;
        flex-direction: column;
        gap: 0;
    }

    .heroSection h1 {
        font-weight:400;
        height: 250px;
    }

    #hoverme {
        display: none;
    }
}

@media (min-width: 768px) and (max-width: 1600px) {
    .heroSection{
        font-size: 2.5rem;
    }
}

@media (min-width: 1600px) {
    .heroSection{
        font-size: 4rem;
    }
}