.info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    color: 141414;
    background-color: #ffffff;
    padding: 10% 5%;
    font-family: 'Manrope';
    gap: 24px;
}

.skills img {
    width: 100px;
    height: 100px;
}

#boldT {
    color: #141414;
    font-weight: 800;
}

.icons-skills {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 6px;
}

.info h2 {
    color: #383838;
    letter-spacing: 0.2px;
    margin-top: 12px;
    font-weight: 500;
    line-height: 2.5rem;
    width: 90%;
    overflow: hidden;
}

.info span {
    color: #8b8b8b;
    overflow: hidden;
}

@media (max-width: 728px) {
    .info {
        padding: 20% 5%;
    }

    .info h2 {
        font-size: medium;
        line-height: 1.5rem;
    }
}